import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import '../components/ModalPayment.css'

const ModalPay = (args) => {
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    return (
        <div>
            <Button color="primary p-3" style={{ fontSize: '1.2rem' }} onClick={toggle}> </Button>
            <Modal isOpen={modal} toggle={toggle} {...args}>
                <ModalHeader toggle={toggle}></ModalHeader>
                <ModalBody>

                    <img src={require('../assets/qr.jpg')} alt='QR code for payment' width='40%'></img>
                    <p>' :  I 
                         : 44828370
                           IBAN: UA843052990000026006016231729
                         :   ""</p>
                </ModalBody>
                <ModalFooter>
                    <Button color="primary donate-button" onClick={toggle}>OK</Button>
                </ModalFooter>
            </Modal>
        </div>
    );
}

export default ModalPay;